html,body {
  width: 100vw;

}

body {
  background-color: rgb(255, 255, 255);
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
.container {

  width: 70%;
  margin: 0 auto;
}

.first{
  background-image: url('./a/hero1.png');
  background-size: cover; 
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}

.headermenu1 {
  background-image: url('./a/whitemenu.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 6rem;
}
.nav {
  padding-top: 1rem;
  display: flex;
  justify-content: space-between;
}

.logoContainer {
  background-image: url('./a/Logo.png');
  background-size: cover; 
  background-position: center;
  width: 1.3rem;
  height: 1.3rem;
}

.stickyNav {
  width:100%;
}

.logo2 {
  width: 100px;
  padding-left: 15%;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  background-color: rgb(255, 255, 255);
}
.nonSticky {
  background-color: rgb(255, 255, 255);
  
}
nav {
  padding: 10px 0;
  width: 100%;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  
}
.right{
  display: flex;
  padding-right: 15%;
}
.menu1 {
  background-image: url('./a/bluemenu.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 6rem;
}
li {
  margin-right: 20px;
  list-style: none;
  font-size: .6rem;
  text-decoration: underline;
  font-weight: bold;
  font-style: italic;
}
.w {
  color: white;
}
.b {
  color: black;
  padding-top: 1rem;

}
.bl {
  color: rgb(72, 72, 72);

  padding-top: .75rem;
}
.wl{
  color: white;

  padding-top: .2rem;
}
a {
  text-decoration: none;
}
.slick-slide img {
  margin: 0 0px 0 0px;
}

.secondWrapper {
  height: 70%
}

.no1{
  background-image: url('./a/01.png');
  background-repeat: no-repeat;
  background-size: contain;
 
}

.history {
  background-image: url('./a/history.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 10%;
  height: 5rem;
  margin-top: 2.7rem;
  
}

.secondH3 {
  
  font-size: .4rem;
  color: rgb(133, 133, 133);
  margin: 0 10px 0 9px;
}

.filterWrapper {
  padding-top: 6.21rem;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.filter {
  filter: opacity(.4);
  background-color: rgb(255, 255, 255);
  width: 100vw;
  height: 120px;
  position: absolute;
}

.dots {
  position: relative;
  width: 80%;
  display: flex;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
  

}
.text {
  width: 70%;
  margin: auto;
  margin-bottom: 10rem;
}

.firstH3 {
  font-size: .4rem;
  color: rgb(133, 133, 133);
  margin: 0 0% 0 9px;
}

h3{
  font-size: .4rem;
  color: rgb(133, 133, 133);
  margin: 0 0% 0 9px;
}


.fixedBox {
  padding-top: 4rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: flex-end;
  
}

.fixedBox2{
  padding-top: 1rem;
  margin: 0;
  width: 500px;
  display: flex;
  align-items: flex-end;
}

.main {
  background-image: url('./a/main.png');
  background-size: cover; 
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
}

.climb {
  height: 70px;
  background-color: rgb(255, 255, 255);
  padding: 1rem 0 1rem 0;
}

.third {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgb(255, 255, 255);
}

.tabsWrapper {
  background-color: #7d8698;
  width: 100%;
  position: relative;
}

.tabList {
  justify-content: flex-start;
  align-items: center;
  margin-left: 15%;
 
}
.tab1{
  padding-top: .2rem;
  width: 3rem;
  margin: .3rem;
  align-items: center;
}
.tab2{

  padding-top: .2rem;
  width: 3rem;
  margin: .3rem;
  align-items: center;
}
.react-tabs__tab--selected {
  background-color: #e0dede;
  color: #fff;
  border: none;
}


.s1{
  width: 50vw;
  position: absolute;
  top: 100px;
  left: 25%;
}
.s2{
  width: 50vw;
  position: absolute;
  top: 100px;
  left: 25%;
}
.footer {
  margin: 0;
  background-color: rgb(255, 255, 255);
  height: 50px;
  filter: brightness(.5) saturate(0);
}

.footerLogo {
  width: 100px;
  padding-left: 9%;
  padding-top: .3rem;
}
.copy
{
  width: 100px;
  float: right;
  padding-top: 1.3rem;
  padding-right: 10%;
}

.react-accessible-accordion__button::before {
  display: none !important;
}

.accbtn1 {
  width: 50px;
}

.accbtn2 {
  width: 50px;
}

.accContent1{
  width: 100%;
}

.accContent2{
  width: 100%;
}

@media screen and (max-width: 600px) {
  .accordionWrapper {
    display: block;
  }
  .tabsWrapper {
    display: none;
  }
}

@media screen and (min-width: 600px) {
  .accordionWrapper {
    display: none;
  }
  .tabsWrapper {
    display: block;
  }
}

@media screen and (min-width: 600px) and (max-width: 900px) {

}
